                <div class="col-sm-3">
                  <button
                    type="button"
                    class="btn btn-primary btn-outline btn-rounded"
                    data-dismiss="modal"
                    @click="gotoCreateCol()"
                  >
                    {{ $t("create-collection") }}
                  </button>
                </div>
                <div class="col-sm-9"></div>
              </div> -->
            </div>
          </div>
          <div class="modal-footer">
            <button
