                    </th>
                    <th
                      @click="sortTable('unit')"
                      class="pointer"
                      v-if="courseInfo.cntUnit !== 0"
                    >
                      {{ $t("unit") }}
                      <i class="zmdi zmdi-swap-vertical ml-1 zmdi-hc-lg"></i>
                    </th>
                    <th @click="sortTable('resource_name')" class="pointer">
                      {{ $t("material") }}
                      <i class="zmdi zmdi-swap-vertical ml-1 zmdi-hc-lg"></i>
                      <!-- <i class="zmdi zmdi-swap-vertical ml-1"></i> -->
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="tb in copyTextbookList" :key="tb.resourceid">
                    <td>
                      <div class="custom-control custom-checkbox form-check">
                        <input
                          type="checkbox"
