                        />
                        <!-- {{ tb.resourceid }} -->
                        <label
                          class="custom-control-label"
                          :for="tb.resourceid"
                        ></label>
                        <!-- {{ openedTextbookList[index] }} -->
                        <!-- @click="
                            onClickWithShift($event, index, tb.resourceid)
                          " -->
                      </div>
                    </td>
                    <td v-if="courseInfo.cntLevel !== 0">
                      <span v-if="tb.level !== ''"
