                    <td v-if="courseInfo.cntUnit !== 0">
                      <span v-if="tb.unit !== ''"
                        >{{ $t("unit") }} {{ tb.unit }}</span
                      >
                      <span v-else></span>
                    </td>
                    <td>
                      <!-- <span v-if="tb.unit !== ''" style="font-size: 14px"
                        >{{ tb.unit }} -
                      </span> -->
                      {{ tb.resource_name }}
                    </td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary btn-outline btn-rounded"
              data-dismiss="modal"
            >
              {{ $t("cancel") }}
            </button>
            <button
              type="button"
              class="btn btn-primary btn-rounded"
              data-dismiss="modal"
              @click="multiOpenSetting()"
            >
              {{ $t("save") }}
