              </div>
              <div class="form-group row">
                <label class="control-label text-right col-sm-3">{{
                  $t("material")
                }}</label>
                <div class="col-sm-9">
                  <div class="bg-light rounded" data-scroll="dark">
                    <ul class="sequence a-sequence">
                      <draggable
                        class="list-group"
                        tag="ul"
                        v-model="tempAList"
                        v-bind="dragOptions"
                        @start="drag = true"
                        @end="drag = false"
                      >
                        <li
                          v-for="(ta, index) in tempAList"
                          :key="index"
                          class="
                            d-flex
                            justify-content-between
                            border
                            bg-white
                            rounded
                          "
                        >
                          <div class="d-flex justify-content-start">
                            <div class="btn btn-nostyle btn-move">
                              <i class="la la-ellipsis-v"></i
                              ><i class="la la-ellipsis-v"></i>
                            </div>
                            <span
                              v-if="ta.note === 'book'"
                              class="row col-sm-12"
                            >
                              <span
                                class="
                                  badge badge-pill badge-primary
                                  fix-badge-height
                                  mr-1
                                "
                                >{{ $t("reading") }}</span
                              >
                              <span class="d-flex align-items-center">{{
                                ta.resource_name
                              }}</span>
                            </span>
                            <div
                              v-else-if="ta.note === 'video'"
                              class="row col-sm-12"
                            >
                              <div>
                                <span
                                  class="
                                    badge badge-pill badge-warning
                                    fix-badge-height
                                    mr-1
