                                  >{{ $t("watching") }}</span
                                >
                              </div>
                              <span class="d-flex align-items-center">
                                {{
                                  ta.resource_name + " - " + ta.material_name
                                }}
                              </span>
                            </div>
                            <span v-else class="row col-sm-12">
                              <span
                                class="
                                  badge badge-pill badge-accent
                                  fix-badge-height
                                  mr-1
                                "
                                >{{ $t("speaking-quiz") }}</span
                              >
                              <span class="d-flex align-items-center"
                                >{{ ta.resource_name }} -
                                <span v-if="ta.material_name !== 'undefined'">{{
                                  ta.material_name
                                }}</span>
                                <span v-else>{{ $t("book") }}</span>
                              </span>
                            </span>
                          </div>

                          <i
                            @click="removeAssignment(ta.note, ta)"
                            class="
                              zmdi zmdi-minus-circle zmdi-hc-2x
                              text-secondary
                              pointer
                            "
                          ></i>
                          <!-- </button> -->
                        </li>
                      </draggable>
                    </ul>
                  </div>
                </div>
              </div>
              <div class="form-group row">
                <label class="col-form-label col-sm-3 text-right">{{
                  $t("due")
                }}</label>
                <div class="col-sm-8">
                  <date-picker
                    v-model="AssignmentDue"
                    valueType="format"
                    range
                    :disabled-date="disabledBeforeToday"
                  ></date-picker>
                  <!-- <input
                    type="text"
                    class="form-control date-picker-input"
                    placeholder="Select Date"
                    v-model="AssignmentDue"
                  /> -->
                </div>
                <!-- <div class="col-2 pt-2 pl-0">{{ AssignmentDue }}</div> -->
              </div>
              <div class="form-group row align-items-start">
                <label class="control-label text-right col-sm-3"></label>
                <small class="col-sm-7"
                  ><span class="text-danger">*</span>
                  {{
                    $t("press-and-hold-the-ctrl-key-for-multiple-selections")
                  }}.</small
                >
              </div>
              <div class="form-group row col-sm-12 mb-5">
                <label class="control-label text-right col-sm-3">{{
                  $t("for-students")
                }}</label>
                <div class="col-sm-9">
                  <!-- <select2
                    id="s2_student"
                    :options="studentList"
                    v-model="selectStudent"
                    :settings="{ multiple: true }"
                  >
                  </select2> -->
                  <div v-if="showStuTable" class="col-sm-12 row">
                    <!-- <label class="control-label text-right col-sm-3"></label> -->
                    <div class="col-sm-12">
                      <input
                        type="text"
                        id="searchStd"
                        class="form-control form-control-lg"
                        placeholder="Search student name"
                        v-model="searchstdName"
                      />
                    </div>
                    <div
                      class="custom-control custom-checkbox col-sm-12 ml-3 mt-1"
                    >
                      <input
                        type="checkbox"
                        class="custom-control-input"
                        id="std"
                        v-model="selectAllS"
                        name="std"
                      />
                      <label class="custom-control-label" for="std">
                        <strong>{{ $t("all-students") }}</strong>
                      </label>
                    </div>
                    <!-- filterStdList    studentList -->
                    <div
                      v-for="(stu, index) in filterStdList"
                      :key="stu.id + index"
                      class="col-sm-4"
                    >
                      <span class="custom-control custom-checkbox">
                        <input
                          type="checkbox"
                          class="custom-control-input"
                          :id="stu.id"
                          :value="stu.id"
                          v-model="selectStudent"
                        />
                        <label class="custom-control-label" :for="stu.id">{{
                          stu.text
                        }}</label>
                      </span>
                    </div>
                  </div>
                </div>
                <!-- 
                <div
                  class="col-sm-2 px-0 align-items-center d-flex hover-expand"
                >
                  <i
                    v-if="showStuTable === false"
                    class="zmdi zmdi-unfold-more pointer hover-blue mr-1"
                    @click="showStuTable = !showStuTable"
                  ></i>
                  <i
                    v-else
                    class="zmdi zmdi-unfold-less pointer hover-blue mr-1"
                    @click="showStuTable = !showStuTable"
                  ></i>
                  <label
                    @click="showStuTable = !showStuTable"
                    class="form-check-label pointer hover-blue"
                    for="expand"
                  >
                    <span v-if="showStuTable === false">{{
                      $t("expand-list")
                    }}</span>
                    <span v-else>{{ $t("close-list") }}</span>
                  </label>
                </div> -->
              </div>
              <!-- :style="{
                  height: selectStudent.length * 7.8 + 'px',
                }" -->
            </form>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary btn-outline btn-rounded"
              data-dismiss="modal"
            >
              {{ $t("close") }}
            </button>
            <button
              type="button"
              class="btn btn-primary btn-rounded"
              data-dismiss="modal"
              data-toggle="modal"
              data-target="#AssignmentModal-2"
              :disabled="AssignmentSetting"
            >
              {{ $t("next") }}
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- AssignmentModal-2 MODAL-->
    <div
      class="modal fade"
      id="AssignmentModal-2"
      tabindex="-1"
      role="dialog"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="ModalTitle1">{{ $t("assignment") }}</h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" class="zmdi zmdi-close"></span>
            </button>
          </div>
          <div class="modal-body">
            <h5>{{ $t("how-difficult-is-this-assignment") }}?</h5>
            <p>
              {{
                $t(
                  "system-will-send-more-rewards-to-more-difficult-assignment"
                )
              }}.
            </p>
            <p
              v-for="(reward, index) in rewardList"
              :key="reward.exp"
              class="mb-0 monospace"
            >
              {{ $t("level") }}
              <span :class="`yellow-${index + 1}`">{{ index + 1 }} </span>

              => {{ $t("exp") }}.
              <span style="font-weight: bold">{{ reward.exp }}</span>

              , {{ $t("lucky-draw-ticket") }}
              <span style="font-weight: bold">{{ reward.ticket }}</span>
            </p>
            <div class="text-center mt-4 mb-4">
              <div class="form-check form-check-inline text-primary">
                {{ $t("easy") }}
              </div>
              <div
                class="custom-control custom-radio custom-control-inline mx-2"
                v-for="(d, i) in difficultList"
                :key="d"
              >
                <input
                  type="radio"
                  :id="d"
                  name="RadioInline"
                  class="custom-control-input pointer"
                  v-model="difficult"
                  :value="d"
                />
                <label
                  class="custom-control-label pointer"
                  :class="`yellow-${i + 1}`"
                  :for="d"
                  >{{ d }}</label
                >
              </div>

              <div class="form-check form-check-inline text-primary">
                {{ $t("difficult") }}
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary btn-outline btn-rounded"
              data-dismiss="modal"
            >
              {{ $t("cancel") }}
            </button>
            <button
              type="button"
              class="btn btn-primary btn-rounded"
              data-dismiss="modal"
              @click="setAssignment()"
            >
              {{ $t("assign") }}
            </button>
          </div>
        </div>
      </div>
    </div>
    <!-- AssignmentModal-2 MODAL-->
    <div
      class="modal fade"
      id="messageModal"
      tabindex="-1"
      role="dialog"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-dialog-centered" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="ModalTitle1">{{ $t("message") }}</h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true" class="zmdi zmdi-close"></span>
            </button>
          </div>
          <div class="modal-body">
            <p>{{ $t("no-related-library-can-be-added") }}.</p>
            <p>
              {{
                $t(
                  "please-create-a-library-of-the-resource-before-adding-the-material"
                )
              }}
            </p>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary btn-outline btn-rounded"
              data-dismiss="modal"
            >
              {{ $t("close") }}
            </button>
            <button
              type="button"
              class="btn btn-primary btn-outline btn-rounded"
              data-dismiss="modal"
              @click="gotoCreateCol()"
            >
              {{ $t("create-library") }}
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import {
  ApiGetCourseDatail,
  ApiSearchCourseResource,
  ApiGetStudentList,
  ApiGetVideoMaterial,
  ApiSetAssignment,
  ApiGetOpenResource,
  ApiPostMultiOpenSetting
} from "../http/apis/CourseDetail";
import {
  ApiGetCollectionList,
  ApiGetResource,
  ApiAddResource,
  ApideleteResource,
} from "../http/apis/Collection";
// import CourseHeader from "../components/CourseHeader";
import DatePicker from "vue2-datepicker";
import "vue2-datepicker/index.css";
import dayjs from "dayjs";
import $ from "jquery";
import draggable from "vuedraggable";
// import _ from "lodash";
import sortBy from "lodash/sortBy";
import { createLogger } from "vuex";
import courseInfo from '../store/module/courseInfo';

export default {
  name: "CourseMaterial",
  components: {
    CourseHeader: () => import("@/components/CourseHeader.vue"),
    DatePicker,
    draggable,
    SearchGroup: () => import("@/components/SearchGroup.vue"),

    // Multiselect
  },
  data() {
    return {
      courseid: this.$route.params.courseid,
      searchRname: "",
      typeList: [
        { text: "All type", id: "all" },
        { text: "Picture Book", id: "book" },
        { text: "Video", id: "video" },
      ],
      selectType: "all",
      // sortTypeList: [
      //   { text: "Sort by title Asc", id: "title_asc" },
      //   { text: "Sort by title Desc", id: "title_desc" },
      //   { text: "Sort by level Asc", id: "level_asc" },
      //   { text: "Sort by level Desc", id: "level_desc" },
      // ],
      selectSortType: "title_asc",
      searchStatus: false,
      searchRList: [],
      courseid: this.$route.params.courseid,
      // tempAssignmentList: [],
      clicked: [],
      videoMaterialList: [],
      tempRname: "",
      tempVMList: [],
      collectionList: [],
      selectCol: "",
      tempResource: {},
      tempcolid: "",
      textbookList: [],
      studentList: [],
      selectStudent: [],
      tempSelectStudent: [],
      selectAllS: false,
      AssignmentDue: null,
      difficult: "1",
      difficultList: ["1", "2", "3", "4", "5"],
      tempAIDList: [],
      tempAList: [],
      openedTextbookList: [],
      lastCheckedIdx: -1,
      setStudent: false,
      existCollectionName: [],
      pkgid: {},
      showStuTable: true,
      courseInfo: {},
      selectAllTB: "",
      sortStatus: false,
      tempSortItem: "",
      copyTextbookList: [],
      courseOverview: [],
      newAgtName: "",
      mfilter: "",
      tempSearchName: "",
      rewardList: [
        { exp: "100", ticket: "1" },
        { exp: "150", ticket: "1" },
        { exp: "200", ticket: "2" },
        { exp: "250", ticket: "2" },
        { exp: "300", ticket: "3" },
      ],
      setResourceArray: [],
      softStatus: false,
      selectLevelList: [],
      tempLevelList: [],
      tempUnitList: [],
      codeNumber: -1,
      codeNumber2: -1,
      templl: [],
      templu: [],
      searchstdName: "",
    };
  },
  created() {
    this.$store.dispatch("common/setLoading", true);
    //列表資訊從menulift call (為了重複使用)
    this.textbookList = this.textbookLists;
    this.openedTextbookList = this.openedTextbookLists;
    this.studentList = this.studentLists;
    this.tempAIDList = this.tempAIDLists;
    this.tempAList = this.tempALists;
    this.pkgid = this.pkgids;
    this.courseInfo = this.courseInfos;
    this.courseOverview = this.courseOverviews;
  },
  mounted() {
    if (this.openAgt) {
      $("#AssignmentModal").modal("show");
      this.$store.dispatch("courseInfo/setOpenAgt", false);
    }
    $(function () {
      $('[data-toggle="tooltip"]').tooltip();
    });
  },
  watch: {
    courseOverviews() {
      this.courseOverview = this.courseOverviews;
    },
    courseInfos() {
      this.courseInfo = this.courseInfos;
    },
    pkgids() {
      this.pkgid = this.pkgids;
    },
    openedTextbookLists() {
      this.openedTextbookList = this.openedTextbookLists;
    },
    textbookLists() {
      // this.textbookList = this.textbookLists;
      console.log("======textbooklist update");

      this.textbookList = this.sortMList;
    },
    studentLists() {
      this.studentList = this.studentLists;
    },
    selectAllS() {
      if (this.selectAllS) {
        // this.tempSelectStudent = this.selectStudent;
        this.selectStudent = [];
        this.studentList.forEach((std) => {
          this.selectStudent.push(std.id);
          std;
        });
        // this.selectStudent.push("*");
      } else {
        this.selectStudent = this.tempSelectStudent;
      }
    },
    tempAIDLists() {
      this.tempAIDList = this.tempAIDLists;
    },
    tempALists() {
      this.tempAList = this.tempALists;
    },
    searchRname() {
      this.searchCourseResource();
    },
    selectType() {
      this.searchCourseResource();
    },
  },
  computed: {
    filterStdList() {
      if (this.searchstdName !== "") {
        let temp = this.studentList.filter((std) => {
          return std.text
            .toLowerCase()
            .includes(this.searchstdName.toLowerCase());
        });
        return temp;
      } else {
        return this.studentList;
      }
    },
    levelLists() {
      let vm = this;

      this.copyTextbookList.forEach((element, i) => {
        if (
          element.level !== undefined &&
          element.level !== "" &&
          !vm.templl.includes(element.level)
        ) {
          vm.tempLevelList[i] = false;
          vm.templl.push(element.level);
        }
      });
      this.templl.sort();
      return this.templl;
      // this.levelLists.sort()
    },
    unitLists() {
      let vm = this;
 
      this.copyTextbookList.forEach((element, i) => {
        if (
          element.unit !== undefined &&
          element.unit !== "" &&
          !vm.templu.includes(element.unit)
        ) {
          vm.tempUnitList[i] = false;
          vm.templu.push(element.unit);
        }
      });
      this.templu.sort();
      return this.templu;
      // this.levelLists.sort()
    },
    sortMList() {
      let sortMaterial = [];
      let openb = this.textbookLists.filter((item) => {
        return item.openflag === "true" && item.note === "book";
      });
      let openv = this.textbookLists.filter((item) => {
        return item.openflag === "true" && item.note === "video";
      });
      if (this.mfilter === "openm") {
        sortMaterial = this.$_sortMaterial(
          this.openedMList,
          this.selectSortType,
          this.selectLevelList
        );
      } else if (this.mfilter === "openb") {

        sortMaterial = this.$_sortMaterial(
          openb,
          this.selectSortType,
          this.selectLevelList
        );
      } else if (this.mfilter === "openv") {

        sortMaterial = this.$_sortMaterial(
          openv,
          this.selectSortType,
          this.selectLevelList
        );
      } else {
        sortMaterial = this.$_sortMaterial(
          this.textbookLists,
          this.selectSortType,
          this.selectLevelList
        );
      }
      console.log("textbooklist = ");
      console.log(sortMaterial);
      // this.textbookList = sortMaterial;
      return sortMaterial;
    },
    openAgt() {
      return this.$store.state.courseInfo.openAgt;
    },

    sortTypeList() {
      return this.$store.state.common.sortTypeList;
    },
    courseOverviews() {
      return this.$store.state.courseInfo.courseOverview;
    },
    courseInfos() {
      return this.$store.state.courseInfo.courseInfo;
    },
    openedMList() {
      return this.textbookList.filter((item) => {
        return item.openflag === "true";
      });
    },
    openedBookList() {
      return this.textbookList.filter((item) => {
        return item.openflag === "true" && item.note === "book";
      });
    },
    openedVideoList() {
      return this.textbookList.filter((item) => {
        return item.openflag === "true" && item.note === "video";
      });
    },
    closedMList() {
      return this.textbookList.filter((item) => {
        return item.openflag !== "true";
      });
    },
    filterCollectionList() {
      this.existCollectionName = [];
      //有哪些collection可以加入這裡LIST
      return this.collectionList.filter((item) => {
        if (item.rid === this.tempResource.resourceid) {
          //排除已經被加入collection
          this.existCollectionName.push(item.text);
        }
        return item.rid !== this.tempResource.resourceid;
      });
    },
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost",
      };
    },
    AssignmentSetting() {
      if (
        this.AssignmentDue === null ||
        this.selectStudent.length <= 0 ||
        this.newAgtName === ""
      ) {
        return true;
      } else {
        if (this.AssignmentDue.includes(null)) {
          return true;
        } else {
          return false;
        }
      }
    },
    pkgids() {
      return this.$store.state.courseInfo.courseInfo.pkgid;
    },
    openedTextbookLists() {
      return this.$store.state.courseInfo.openedTextbookList;
    },
    textbookLists() {
      return this.$store.state.courseInfo.textbookList;
    },
    studentLists() {
      return this.$store.state.courseInfo.forSelectStudentList;
    },
    userid() {
      return this.$store.state.auth.userid;
    },
    tempAIDLists() {
      return this.$store.state.courseInfo.caidList;
      // return this.$store.state.courseInfo.tempAIDList[this.courseid] === undefined ?  this.tempAIDList : this.$store.state.courseInfo.tempAIDList[this.courseid] ;
    },
    tempALists() {
      return this.$store.state.courseInfo.caList;
      //  return this.$store.state.courseInfo.tempAList[this.courseid] === undefined ?  this.tempAList : this.$store.state.courseInfo.tempAList[this.courseid] ;
      // return this.$store.state.courseInfo.tempAList[this.courseid];
    },
  },
  methods: {
    // select by shift end
    filterLevel(level) {
      if (this.codeNumber !== level) {
        this.copyTextbookList = this.copyTextbookList.filter((item) => {
          return item.level.indexOf(level) !== -1;
        });
      } else {
        this.codeNumber = -1;
        this.copyTextbookList = [...this.textbookList];
      }
    },
    filterUnit(unit) {
      if (this.codeNumber2 !== unit) {
        this.copyTextbookList = this.copyTextbookList.filter((item) => {
          return item.unit.indexOf(unit) !== -1;
        });
      } else {
        this.codeNumber2 = -1;
        this.copyTextbookList = [...this.textbookList];
      }
    },
    getMList(textbookList) {
      this.textbookList = textbookList;
      this.softStatus = true;
      this.$store.dispatch("common/setLoading", false);
    },
    getFilterInfo(selectLevelList, selectSortType) {
      console.log("sort type");
      console.log(selectSortType);
      let sortType = selectSortType.split("_")[0];
      if (sortType === "title") {
        this.tempSortItem = "resource_name";
      }
      this.selectLevelList = selectLevelList;
      this.selectSortType = selectSortType;
    },
    changemfilter(filterName) {
      if (filterName === this.mfilter) {
        this.mfilter = "";
      } else {
        this.mfilter = filterName;
      }
    },
    sortTable(sortItem) {
      if (this.tempSortItem === sortItem) {
        this.copyTextbookList = this.copyTextbookList.reverse();
      } else {
        this.tempSortItem = sortItem;
        this.copyTextbookList = sortBy(
          this.copyTextbookList,
          [(obj) => obj[sortItem]],
          ["asc"]
        );
      }

    },
    disabledBeforeToday(date) {
      let today = new Date();
      today.setHours(0, 0, 0, 0);

      return date < today;
    },
    openAssignmentModal() {
      if (this.studentList.length <= 0) {
        this.$bus.$emit(
          "messsage:push",
          "Please add students to the course first.",
          "danger"
        );
      } else {
        $("#AssignmentModal").modal("show");
      }
    },
    // multiple select
    addTag(newTag) {
      const tag = {
        text: newTag,
        id: newTag.substring(0, 2) + Math.floor(Math.random() * 10000000),
      };

    },
    async getCollectionList(rid, obj) {
      let vm = this;
      this.tempResource = obj;
      this.collectionList = [];
      let result = await ApiGetCollectionList.get(this.userid, rid)
        .then((response) => {
          this.collectionList = response.record.map((o) => {
            return {
              id: o.collectionid,
              text: o.collection_name,
              rid: o.resourceid,
            };
          });
          if (response.status === "success") {
            return true;
          }
        })
        .catch((err) => {});
      if (result) {
        if (
          vm.existCollectionName.length !== 0 ||
          vm.filterCollectionList.length !== 0
        ) {
          $("#addtoColletion").modal("show");
        } else {
          $("#messageModal").modal("show");
        }
      }
    },

    addResource() {
      this.selectCol = this.selectCol.toString();
      this.tempResource.resourceid = this.tempResource.resourceid.toString();
      ApiAddResource.get(this.selectCol, this.tempResource.resourceid)
        .then((response) => {
          console.log(response);
          if (response.status === "success") {
            this.$bus.$emit(
              "messsage:push",
              "Successful addition to the collection.",
              "success"
            );
            this.selectCol = "";
          } else {
            this.$bus.$emit("messsage:push", "Unknown error.", "danger");
            this.selectCol = "";
          }
        })
        .catch((err) => {});
    },

    addToAssignmentList(m) {
      // this.clicked.push(m.resourceid);
   
      this.$store.dispatch("courseInfo/setAssignment", {
        assignment: m,
        id: m.resourceid,
      });
    },
    searchCourseResource() {
      //判斷是否有搜尋值
      if (this.searchRname !== "") {
        this.searchStatus = true;
        this.tempSearchName = this.searchRname;
      } else {
        this.searchStatus = false;
      }
      let sType = this.selectType;
      if (this.selectType === "all") {
        sType = "*";
      }
      let searchObj = {
        type: sType,
        keyword: this.searchRname,
      };
      ApiSearchCourseResource.post(this.courseid, searchObj)
        .then((response) => {
          response.record.forEach((element) => {
            if (element.unit === undefined) {
              element.unit = "";
            }
            element.link = element.information.includes("http");
          });
          this.textbookList = response.record;
        })
        .catch((err) => {});
    },
    async getVideoMaterial(Ncolid, rid) {
      let colid = Ncolid.split(";")[0];
      let result = await ApiGetVideoMaterial.get(colid, this.courseid, rid)
        .then((response) => {
          this.videoMaterialList = response.record;
          if (response.status === "success") {
            return true;
          }
        })
        .catch((err) => {});
      if (result) {
        this.videoMaterialList.forEach((el) => {
          el.resource_name = this.tempRname;
        });
      }
    },
    addtoSequence() {
      //e現在的格式 => MID_RID
      //新增一個ASS +  本來就在系統暫存的

      let result = this.tempVMList
        .filter((e) => {
          return this.tempAIDList.indexOf(e) === -1;
        })
        .concat(
          this.tempAIDList.filter((f) => {
            return this.tempVMList.indexOf(f) === -1;
          })
        );

      //取絕對差集 補集
      // result = id array
      //SET的時候傳整個OBJECT 所以要從ID列表得到整個OBJET
      //從ID => GET OBJECT

      //只需要MIDLIST 格式轉換 MID_RID => MID
      let copyVMList = [];
      result.forEach((item) => {
        item = item.split("_")[0];
        copyVMList.push(item);
      });

      let vmList = [];
      this.videoMaterialList.forEach((element) => {
        if (copyVMList.includes(element.materialid)) {
          vmList.push(element);
        }
      });

      // result要排序
      vmList.sort((a, b) => {
        return ("" + a.material_name).localeCompare(b.material_name);
      });

      //如果變動的本就存在 從暫存移除 不存在則加入暫存
      vmList.forEach((obj) => {
        if (this.tempAIDList.includes(obj.materialid + "_" + obj.resourceid)) {
          this.$store.dispatch("courseInfo/removeAssignment", {
            id: obj.materialid + "_" + obj.resourceid,
          });
        } else {
          this.$store.dispatch("courseInfo/setAssignment", {
            assignment: obj,
            id: obj.materialid + "_" + obj.resourceid,
          });
        }
      });
      this.$bus.$emit(
        "messsage:push",
        "Add to the temporary assignment list",
        "success"
      );
    },
    removeAssignment(type, obj) {
      if (type === "book") {
        this.$store.dispatch("courseInfo/removeAssignment", {
          id: obj.resourceid,
        });
      } else if (type === "video") {
        this.$store.dispatch("courseInfo/removeAssignment", {
          id: obj.materialid + "_" + obj.resourceid,
        });
      } else {
        this.$store.dispatch("courseInfo/removeAssignment", {
          id: obj.quizid,
        });
      }
    },
    gotoWebsite(obj) {
      if (obj.note === "book") {
        window.open(
          `${process.env.VUE_APP_DOMAIN}/bktchr/?pkgid=${this.courseInfo.pkgid}&colid=${this.courseInfo.colid}&resid=${obj.resourceid}&mid=&lmsd=${process.env.VUE_APP_LMSD}&auth=${this.$store.state.auth.token}&crsid=${this.courseInfo.courseid}&userid=${this.userid}&LANG=${this.$i18n.locale}`
        );
      } else {
        window.open(
          `${process.env.VUE_APP_DOMAIN}/vptchr/?pkgid=${this.courseInfo.pkgid}&colid=${this.courseInfo.colid}&resid=${obj.resourceid}&mid=&lmsd=${process.env.VUE_APP_LMSD}&auth=${this.$store.state.auth.token}&crsid=${this.courseInfo.courseid}&userid=${this.userid}&LANG=${this.$i18n.locale}`
        );
      }
    },
    async setAssignment() {
      let obj = {};
      obj.userid = this.userid;
      obj.publish_date = dayjs(this.AssignmentDue[0]).unix();
      obj.expiry_date = dayjs(this.AssignmentDue[1]).unix();
      obj.difficult = this.difficult;
      obj.description = this.newAgtName;
      if (
        this.selectStudent.includes("*") ||
        this.selectStudent.length === this.studentList
      ) {
        obj.student = "all";
      } else {
        obj.student = this.selectStudent.join(";");
      }
      this.tempAList.forEach((el) => {
        if (el.note === "video") {
          this.$set(el, "assignment_type", "watching");
        } else if (el.note === "book") {
          this.$set(el, "assignment_type", "reading");
        } else {
          this.$set(el, "assignment_type", "speaking");
        }
      });
      obj.content = this.tempAList;
      let result = await ApiSetAssignment.post(this.courseid, obj)
        .then((response) => {
          if (response.status === "success") {
            this.$bus.$emit(
              "messsage:push",
              "New assignment success!",
              "success"
            );
            return true;
          } else {
         
            this.$bus.$emit("messsage:push", "Unknown error!!", "danger");
          }
        })
        .catch((err) => {});
      //新增作業成功
      if (result) {
        //移除INDEX
        this.$store.dispatch(
          "courseInfo/clearCourseTempAssignment",
          this.$route.params.courseid
        );
        //新增更新回去
        this.$store.dispatch(
          "courseInfo/updateTextbookList",
          this.$route.params.courseid
        );
        this.newAgtName = "";
        this.selectAllS = false;
        this.selectStudent = "";
        this.AssignmentDue = null;
        this.difficult = "1";
        this.$bus.$emit("messsage:push", "New assignment success.", "success");
      }
    },
    // 舊的多重開關API (目前未使用)
    // async materialOpenSetting() {
    //   this.$store.dispatch("common/setLoading", true);
    //   let promises = [];
    //   let vm = this;
    //   vm.setResourceArray = [];
    //   // //             如果新增OPEN
    //   // // if NEW NOT INCLUEDS OLD
    // openedTextbookList = 現在的ARRAY
    // openedTextbookLists = 舊設定的ARRAY
    //   for (let i = 0; i < this.openedTextbookList.length; i++) {
    //     if (await !vm.openedTextbookLists.includes(vm.openedTextbookList[i])) {
    //       this.getOpenResource(
    //         vm.openedTextbookList[i].split(";")[0],
    //         vm.openedTextbookList[i].split("_")[1],
    //         "true"
    //       );

    //       console.log("push open");
    //     }
    //   }
    //   for (let i = 0; i < this.openedTextbookLists.length; i++) {
    //     if (await !vm.openedTextbookList.includes(vm.openedTextbookLists[i])) {
    //       this.getOpenResource(
    //         vm.openedTextbookLists[i].split(";")[0],
    //         vm.openedTextbookLists[i].split("_")[1],
    //         "false"
    //       );
    //       console.log("push Close");
    //     }
    //   }
    //   let result = await ApiGetOpenResource.getAll(vm.setResourceArray)
    //     .then((response) => {
    //       return true;
    //     })
    //     .catch((err) => {});
    //   if (result) {
    //     console.log("update info");
    //     this.$store.dispatch(
    //       "courseInfo/updateTextbookList",
    //       this.$route.params.courseid
    //     );
    //     this.$store.dispatch("common/setLoading", false);
    //   }
    // },
    async multiOpenSetting(){
      console.log('multi select')
      let vm = this
      let openRes = []
      let closeRes = []
            this.$store.dispatch("common/setLoading", true);
        for (let i = 0; i < this.openedTextbookList.length; i++) {
        if (await !vm.openedTextbookLists.includes(vm.openedTextbookList[i])) {
          openRes.push(vm.openedTextbookList[i].split("_")[1])
        }
      }
      for (let i = 0; i < this.openedTextbookLists.length; i++) {
        if (await !vm.openedTextbookList.includes(vm.openedTextbookLists[i])) {
          closeRes.push(vm.openedTextbookLists[i].split("_")[1])
        }
      }
      openRes = openRes.join('|')
      let result = await ApiPostMultiOpenSetting.post(this.courseInfo.colid,this.$route.params.courseid,openRes,'true')
      .then((response) => {
        console.log(response)
        if(response.status==='success'){
          
          return true
        }
      }).catch((err) => {
        
      });
      closeRes = closeRes.join('|')
      let result2 = await ApiPostMultiOpenSetting.post(this.courseInfo.colid,this.$route.params.courseid,closeRes,'false')
      .then((response) => {
              console.log(response)
        if(response.status==='success'){
          return true
        }
      }).catch((err) => {
        
      });
      if(result && result2){
        this.$store.dispatch(
          "courseInfo/updateTextbookList",
          this.$route.params.courseid
        );
        this.$store.dispatch("common/setLoading", false);
      }
    },
    async getOpenResource(colid, rid, status, single = false) {
      console.log(
        `/course/openresource/${colid}/${this.$route.params.courseid}/${rid}/${status}`
      );
      this.setResourceArray.push(
        `/course/openresource/${colid}/${this.$route.params.courseid}/${rid}/${status}`
      );

      if (single) {
        let result = await ApiGetOpenResource.getAll(this.setResourceArray)
          .then((response) => {
            return true;
          })
          .catch((err) => {});
        if (result) {
          console.log("update page");
          this.$store.dispatch(
            "courseInfo/updateTextbookList",
            this.$route.params.courseid
          );
          this.$store.dispatch("common/setLoading", false);
        }
      }
    },
    gotoSpeakingQuiz(m, rname = "") {
      $("#addSpeakingquiz").modal("hide");
      let mType = "video";
      if (m.note === "book") {
        mType = "book";
      }
      let url = `/quiz/${this.$route.params.course}/Speaking Quiz/${rname}/${m.material_name}/${this.courseid}/${mType}/${m.resourceid}/${m.materialid}`;
      url = url.replaceAll("?", "%3F");
      this.$router.push({
        path: url,
      });
    },
    gotoCreateCol() {
      this.$router.push({
        path: `/collection/create/${this.pkgid}`,
      });
    },
    copyMArray() {
      this.copyTextbookList = [...this.textbookList];
    },
    selectAllTBft(event) {
      const vm = this;

      if (!event.currentTarget.checked) {
        vm.openedTextbookList = [];
      } else {
        //實現全選
        vm.openedTextbookList = [];
        vm.textbookList.forEach(function (item, i) {
          vm.openedTextbookList.push(`${item.colid}_${item.resourceid}`);
        });
      }
    },
  },
};
</script>

